<template>
  <div class="strategy-picker">
    <FormField
      label="战略划分"
      :required="true"
      type="select"
      :placeholder="editable ? '请选择' : displayText"
      :disabled="!editable"
      :value="displayText"
      @select-click="onOpen"
    />

    <van-popup v-model:show="showPicker" position="bottom" round>
      <van-picker
        :columns="options"
        @confirm="onConfirm"
        @cancel="onCancel"
        :default-index="defaultIndex"
        title="选择战略划分"
      />
    </van-popup>
  </div>
</template>

<script setup>
import { ref, computed, defineProps, defineEmits, watch } from 'vue'
import FormField from '@/components/base/FormField.vue'

const props = defineProps({
  modelValue: { type: String, default: '' },
  editable: { type: Boolean, default: true },
  branchCode: { type: String, default: '' }
})

const emit = defineEmits(['update:modelValue'])

const showPicker = ref(false)
// 这里使用静态示例；若需从后端/字典加载，可对外暴露 props 或在父级注入
import { onMounted } from 'vue'
// 假设有 getStrategyOptions 方法可用，实际应根据项目接口实现
// import { getStrategyOptions } from '@/api/activity'

const options = ref([])

// 模拟接口请求，根据分公司代码获取战略选项
async function fetchStrategyOptions(branchCode) {
  console.log('StrategyPicker - 获取战略选项，分公司代码:', branchCode)
  
  // 模拟不同分公司的战略数据
  const strategyData = {
    '01': [ // 北京分公司
      { text: '北京A战略', value: 'A1' },
      { text: '北京B战略', value: 'B1' }
    ],
    '02': [ // 湖北分公司
      { text: '湖北A战略', value: 'A2' },
      { text: '湖北B战略', value: 'B2' },
      { text: '湖北C战略', value: 'C2' }
    ]
  }
  
  // 模拟API调用延迟
  await new Promise(resolve => setTimeout(resolve, 200))
  
  if (branchCode && strategyData[branchCode]) {
    options.value = strategyData[branchCode]
    console.log('StrategyPicker - 加载的战略选项:', options.value)
  } else {
    options.value = []
    console.log('StrategyPicker - 未找到对应分公司的战略选项')
  }
}

// 监听分公司代码变化，重新加载战略选项
watch(() => props.branchCode, (newVal, oldVal) => {
  console.log('StrategyPicker - 分公司代码变化:', { oldVal, newVal })
  if (newVal && newVal !== oldVal) {
    // 分公司变化时，清空当前战略选择并重新加载
    emit('update:modelValue', '')
    fetchStrategyOptions(newVal)
  } else if (!newVal && oldVal) {
    // 分公司被清空时，清空战略选项
    console.log('StrategyPicker - 分公司被清空，清空战略选项')
    options.value = []
    emit('update:modelValue', '')
  }
}, { immediate: true })

onMounted(() => {
  // 初始化时如果有分公司代码，则加载对应的战略选项
  if (props.branchCode) {
    fetchStrategyOptions(props.branchCode)
  }
})

// 此处 options 已在上方声明并由 fetchStrategyOptions 填充，无需重复声明

const displayText = computed(() => {
  const found = options.value.find(o => o.value === props.modelValue)
  return found ? found.text : ''
})

const defaultIndex = computed(() => {
  const idx = options.value.findIndex(o => o.value === props.modelValue)
  return idx >= 0 ? idx : 0
})

const onOpen = () => {
  if (!props.editable) return
  showPicker.value = true
}

const onConfirm = ({ selectedOptions }) => {
  const select = selectedOptions && selectedOptions[0]
  if (select) emit('update:modelValue', select.value)
  showPicker.value = false
}

const onCancel = () => { showPicker.value = false }
</script>

<style scoped>
.strategy-picker { padding: 0; }
</style>


