<template>
  <div class="category-picker">
    <!-- 活动类别选择器弹窗 -->
    <van-popup v-model:show="showPickerPopup" position="bottom" round>
      <van-picker 
        :columns="categoryOptions" 
        @confirm="onConfirm" 
        @cancel="onCancel" 
        :default-index="0"
        title="选择活动类别"
      />
    </van-popup>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue'

const props = defineProps({
  modelValue: {
    type: String,
    default: ''
  },
  editable: {
    type: Boolean,
    default: true
  }
})

const emit = defineEmits(['update:modelValue'])

// 弹窗控制变量
const showPickerPopup = ref(false)

// 活动类别选项
const categoryOptions = ref([
  { text: '超体场景', value: '1' },
  { text: '服务行销', value: '2' }
])

// 显示选择器
const showPicker = () => {
  if (!props.editable) return
  showPickerPopup.value = true
}

// 确认选择
const onConfirm = ({ selectedOptions }) => {
  const selectCategory = selectedOptions[0]
  if (selectCategory && selectCategory.text) {
    emit('update:modelValue', selectCategory.value)
  }
  showPickerPopup.value = false
}

// 取消选择
const onCancel = () => {
  showPickerPopup.value = false
}

// 暴露方法给父组件使用
defineExpose({
  showPicker
})
</script>

<style scoped>
.category-picker {
  /* 组件样式 */
}
</style>
