<template>
  <div class="health-relate-picker">
    <!-- 健保通相关选择器弹窗 -->
    <van-popup v-model:show="showPickerPopup" position="bottom" round>
      <van-picker 
        :columns="healthRelateOptions" 
        @confirm="onConfirm" 
        @cancel="onCancel" 
        :default-index="0"
        title="选择是否健保通相关"
      />
    </van-popup>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue'

const props = defineProps({
  modelValue: {
    type: String,
    default: ''
  },
  editable: {
    type: Boolean,
    default: true
  }
})

const emit = defineEmits(['update:modelValue'])

// 弹窗控制变量
const showPickerPopup = ref(false)

// 健保通相关选项
const healthRelateOptions = ref([
  { text: '否', value: '0' },
  { text: '是', value: '1' }
])

// 显示选择器
const showPicker = () => {
  if (!props.editable) return
  showPickerPopup.value = true
}

// 确认选择
const onConfirm = ({ selectedOptions }) => {
  const selectHealthRelate = selectedOptions[0]
  if (selectHealthRelate && selectHealthRelate.text) {
    emit('update:modelValue', selectHealthRelate.value)
  }
  showPickerPopup.value = false
}

// 取消选择
const onCancel = () => {
  showPickerPopup.value = false
}

// 暴露方法给父组件使用
defineExpose({
  showPicker
})
</script>

<style scoped>
.health-relate-picker {
  /* 组件样式 */
}
</style>
